* {
  text-decoration: none;
  list-style-type: none;
}

nav {
  z-index: 1000;
  min-width: 100%;
  padding: 0 1rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: fixed;
  background-color: #151515;
}

.mainLogo {
  width: 2.5rem;
}

.navLinks {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 2rem;
}

.navLinks li {
  color: white;
  font-family: 'Roboto', sans-serif;
  position: relative;
}

.navLinks li a {
  color: white;
}

.navLinks li a:hover {
  color: greenyellow;
}

.navLinks li:hover {
  color: greenyellow;
  cursor: pointer;
}

.navLinks li::after {
  content: '';
  position: absolute;
  bottom: -1px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: greenyellow;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transform-origin: left;
          transform-origin: left;
  -webkit-transition: all 400ms ease-in-out;
  transition: all 400ms ease-in-out;
}

.navLinks li:hover::after {
  color: greenyellow;
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transform-origin: right;
          transform-origin: right;
}

.loginBtn {
  margin-right: 2rem;
  background-color: greenyellow;
  color: black;
  padding: .5rem 1rem;
  border-radius: .5rem;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  font-weight: 800;
}

@media only screen and (max-width: 500px) {
  nav {
    padding-top: .5rem;
    max-width: 100%;
    position: relative;
  }
  nav .mainLogo {
    width: 3rem;
  }
  nav .navLinks {
    max-width: 100%;
    position: absolute;
    top: 4rem;
    left: 0;
    right: 0;
    margin: auto;
    gap: 2rem;
    padding: .5rem;
  }
  nav .navLinks li {
    font-size: .9rem;
  }
}
