.progress {
    position: relative;
    height: 10px;
    width: calc(100% - 20px);
    border: 10px solid greenyellow;
    border-radius: 15px;
}

.progress .color {
    position: absolute;
    background-color: #3e3e3e;
    width: 0px;
    height: 10px;
    border-radius: 15px;
    animation: progres 4s infinite linear;
}

@keyframes progres {
    0% {
        width: 0%;
    }

    25% {
        width: 50%;
    }

    50% {
        width: 75%;
    }

    75% {
        width: 85%;
    }

    100% {
        width: 100%;
    }
}

